@import url("https://fonts.googleapis.com/css?family=Open+Sans:regular,700");

@import url("https://fonts.googleapis.com/css?family=Roboto:300");

@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300italic,regular,900");

* {
  margin: 0;
  padding: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus.active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input {
  outline: none;
}

button {
  outline: none;
}

textarea {
  outline: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

ol {
  list-style-position: inside;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
  margin: 0;
}

@font-face {
  font-family: "Big John";
  font-display: swap;
  src: url("../fonts/BIG JOHN.woff") format("woff"), url("../fonts/BIG JOHN.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

html {
  min-height: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  height: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.lock {
  overflow: hidden;
}

.container {
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.logo {
  font-family: "Big John";
  font-weight: 700;
  font-size: 25px;
  color: #fff;
}

.header {
  position: relative;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: url(../img/header/header-bg.jpeg) center/cover no-repeat;
}

.header::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.404);
  z-index: 1;
}

.header__info {
  position: relative;
  z-index: 2;
  text-align: center;
}

.header__title {
  color: #fff;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 300;
  font-size: 35px;
  letter-spacing: 5px;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.header__promo {
  color: #fff;
  padding-bottom: 60px;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 1.2;
}

.header__scroll-link {
  position: absolute;
  z-index: 3;
  bottom: 70px;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 37px;
  height: 37px;
  border-radius: 50%;
  background-color: #0a856a;
  transform: translateX(-50%);
  -webkit-animation: hoverWave linear 2.5s infinite;
  animation: hoverWave linear 2.5s infinite;
}

@-webkit-keyframes hoverWave {
  0% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  40% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 15px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  80% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 26.7px rgba(25, 189, 154, 0.067);
  }

  100% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 40px rgba(25, 189, 154, 0);
  }
}

@keyframes hoverWave {
  0% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 0 rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  40% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 15px rgba(25, 189, 154, 0.2), 0 0 0 0 rgba(25, 189, 154, 0.2);
  }

  80% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 26.7px rgba(25, 189, 154, 0.067);
  }

  100% {
    box-shadow: 0 8px 10px rgba(25, 189, 154, 0.3), 0 0 0 30px rgba(25, 189, 154, 0), 0 0 0 40px rgba(25, 189, 154, 0);
  }
}

.nav {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  transition: all 0.2s ease;
}

.nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 2;
  transition: all 0.2s ease;
}

.nav__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 114px;
  align-items: center;
  transition: all 0.2s ease;
}

.nav__logo {
  position: relative;
  z-index: 3;
  padding-bottom: 10px;
  text-transform: uppercase;
}

.nav__burger {
  display: none;
}

.nav__list {
  display: flex;
}

.nav__list li {
  position: relative;
  z-index: 2;
}

.nav__list li:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  display: inline-block;
  color: #fff;
  padding: 5px 15px;
  border: 1px solid transparent;
  border-radius: 15px;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  transition: all 0.2s ease;
}

.nav__link:hover {
  border: 1px solid #fff;
}

.nav.nav--active::before {
  background: #4b4f14;
  background: -moz-linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
  background: -webkit-linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
  background: linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#4b4f14",endColorstr="#885a05",GradientType=1);
}

.nav.nav--active .nav__body {
  height: 50px;
}

@media (max-width: 991.98px) {
  .container {
    max-width: 960px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .nav__list li:not(:last-child) {
    margin-right: 5px;
  }
}

@media (max-width: 767.98px) {
  .container {
    max-width: 747px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .nav.active::before {
    background-color: #7c573f;
  }

  .nav__body {
    height: 50px;
  }

  .nav__logo {
    padding-left: 15px;
    padding-bottom: 0;
  }

  .nav__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 3;
    position: relative;
  }

  .nav__burger span {
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    left: 0;
    top: 9px;
    transition: all 0.3s ease 0s;
  }

  .nav__burger::before,
  .nav__burger::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .nav__burger::before {
    top: 0;
  }

  .nav__burger::after {
    bottom: 0;
  }

  .nav__burger.active span {
    transform: scale(0);
  }

  .nav__burger.active::before {
    transform: rotate(45deg);
    top: 9px;
  }

  .nav__burger.active::after {
    transform: rotate(-45deg);
    bottom: 9px;
  }

  .nav__menu {
    position: fixed;
    top: -100%;
    left: 0;
    overflow: auto;
    padding: 70px 10px 20px 10px;
    width: 100%;
    height: 100%;
    background: #4b4f14;
    background: -moz-linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
    background: -webkit-linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
    background: linear-gradient(90deg, #4b4f14 0%, #885a05 64%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#4b4f14",endColorstr="#885a05",GradientType=1);
    transition: all 0.3s ease 0s;
  }

  .nav__menu.active {
    top: 0;
  }

  .nav__list {
    display: block;
  }

  .nav__list li {
    margin: 0 0 20px 0px;
  }

  .nav__list li a {
    font-size: 30px;
  }
}

@media (max-height: 991.98px) and (orientation: landscape) and (max-width: 767.98px) {
  .nav__menu {
    padding: 70px 30px 20px 30px;
  }
}

@media (max-height: 991.98px) and (orientation: landscape) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (max-height: 320px) {
  .header__container {
    padding-top: 100px;
  }
}

@media (max-height: 479.98px) {
  .header__scroll-link {
    bottom: 20px;
  }
}